<template>
<div class="">
  <h1></h1>
  <div class="top-group">
    <el-button type="primary" icon="el-icon-plus" @click="onAddProjectButtonClick"></el-button>
  </div>
  <el-table :data="projects" style="width: 100%">
    <el-table-column prop="name" label="" width="180">
      <template slot-scope="scope">
        <router-link :to="'/project/'+scope.row.id">{{scope.row.name}}</router-link>
      </template>
    </el-table-column>
    <el-table-column prop="type" label="" width=100>
    </el-table-column>
    <el-table-column label="">
      <template slot-scope="scope">
        {{scope.row.last_update_time|dateFormat}}
      </template>
    </el-table-column>
    <el-table-column prop="description" label="">
    </el-table-column>
    <el-table-column label="" width="100">
      <template>
        <!-- <el-tag type="success" v-if="scope.row.status==true"></el-tag>
        <el-tag type="danger" v-if="scope.row.status==false"></el-tag> -->
        <el-tag type="success"></el-tag>
      </template>
    </el-table-column>
    <el-table-column label="" width="300">
      <template slot-scope="scope">
        <el-button @click="onEditProject(scope.row)" size="mini"></el-button>
        <el-button type="danger" size="mini"></el-button>
      </template>
    </el-table-column>
  </el-table>

  <el-dialog title="" :visible.sync="addDialogVisiable">
    <el-form ref="projectForm" :model="projectForm" label-width="80px" :rules="projectRules">
      <el-form-item label="" prop="name">
        <el-input v-model="projectForm.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="projectForm.type" placeholder="">
          <el-option label="Web" value="web"></el-option>
          <el-option label="App" value="app"></el-option>
        </el-select>  
      </el-form-item>
      <el-form-item label="" prop="description">
        <el-input type="textarea" v-model="projectForm.description" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="addDialogVisiable = false"> </el-button>
      <el-button type="primary" @click="onSubmitAddProject" :loading="addProjectButtonLoading"> </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
export default {
  name: "ProjectList",
  data() {
    return {
      projects: [],
      addDialogVisiable: false,
      addProjectButtonLoading: false,
      dialogType: "add",
      projectForm: {
        name: "",
        type: "",
        description: ""
      },
      projectRules: {
        name: [
          {required: true,message: "",trigger: "blur"}
        ],
        type: [
          {required: true,message: "",trigger: "blur"}
        ]
      }
    }
  },
  components: {},
  mounted(){
    this.$http.getProjectList().then(res => {
      const projects = res.data
      this.projects = projects
    })
  },
  methods: {
    initProjectForm(){
      this.projectForm = {
        name: "",
        type: "",
        description: ""
      }
    },
    onAddProjectButtonClick(){
      this.dialogType = 'add'
      this.addDialogVisiable = true
    },
    onEditProject(project){
      this.projectForm = {
        name: project.name,
        type: project.type,
        description: project.description,
        id: project.id
      }
      this.addDialogVisiable = true
      this.dialogType = "edit"
    },
    onSubmitAddProject(){
      this.$refs['projectForm'].validate(valid => {
        if(!valid){
          return
        }
        this.addProjectButtonLoading = true
        if(this.dialogType == 'add'){
          this.$http.addProject(this.projectForm).then(res => {
            this.addProjectButtonLoading = false
            if(res && res.status == 201){
              const project = res.data
              this.projects.push(project)
              this.addDialogVisiable = false
              this.initProjectForm()
              this.$message.success("")
            }
          })
        }else{
          this.$http.editProject(this.projectForm.id,this.projectForm).then(res => {
            this.addProjectButtonLoading = false
            if(res){
              this.addDialogVisiable = false
              this.initProjectForm()
              this.$message.success("")
              const project = res.data
              let index = 0
              for(let loop_project of this.projects){
                console.log(loop_project);
                console.log('xxxxx');
                if(loop_project.id == project.id){
                  Vue.set(this.projects,index,project)
                  break
                }
                index++
              }
            }
          })
        }
      })
    }
  }
}
</script>

<style scoped lang='scss'>
.top-group{
  padding: 10px 0;
  text-align: right;
}
</style>