import {
  MessageBox
} from "element-ui"

class ZLMessageBox {
  constructor() {}

  confirm(params = {}) {
    const message = params.message || ""
    const confirmCallback = params.confirmCallback || null
    const cancelCallback = params.cancelCallback || null
    const type = params.type || "warning"
    MessageBox.confirm(message, "", {
      confirmButtonText: '',
      cancelButtonText: '',
      type: type
    }).then(() => {
      if (confirmCallback) {
        confirmCallback()
      }
    }).catch(() => {
      if (cancelCallback) {
        cancelCallback()
      }
    })
  }
}

export default new ZLMessageBox()
