<template>
<div class="">
  <h1 style="padding: 10px 0"></h1>
  <div class="form-group">
    <el-form ref="form" :model="form" label-width="80px" :rules="rules" label-position="left">
      <el-form-item label="" prop="username">
        <el-input v-model="form.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="telephone">
        <el-input v-model="form.telephone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="avatar-upload"
          :action="upload_action"
          :header="headers"
          :show-file-list="false"
          :on-success="onUploadSuccess"
          :before-upload="onBeforeUpload"
        >
          <img v-if="form.avatar" :src="form.avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-upload-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button type="default" @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: "Settings",
  data() {
    return {
      form: {
        username: "",
        email: "",
        telephone: "",
        avatar: ""
      },
      rules:{
        username: [{required: true,trigger:"blur",message:""}],
        email: [{required: true,trigger:"blur",message:""}],
        telephone: [{required: true,trigger:"blur",message:""}],
      },
      upload_action: "http://127.0.0.1:8000/auth/avatar",
      headers: {
        'Authorization': "JWT " + this.$auth.token
      }
    }
  },
  components: {},
  mounted(){
    const form = {
      username: this.$auth.user.username,
      telephone: this.$auth.user.telephone,
      email: this.$auth.user.email,
      avatar: this.$auth.user.avatar
    }
    this.form = form
  },
  methods: {
    onUploadSuccess(res,file){
      this.form.avatar = res.picture
    },
    onBeforeUpload(file){
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        alert(' JPG !');
      }
      if (!isLt2M) {
        alert(' 2MB!');
      }
      return isJPG && isLt2M;
    },
    onSubmit(){
      this.$refs['form'].validate(valid => {
        if(!valid){
          return
        }
        this.$loading.show()
        this.$http.editUser(this.form).then(res => {
          const user = res.data
          this.$auth.setUser(user)
          this.$loading.hide()
          this.$message.success()
        })
      })
    },
    onCancel(){}
  }
}
</script>

<style scoped lang='scss'>
.form-group{
  width: 400px;
}

.avatar-upload{
  .avatar {
    width: 133px;
    height: 133px;
    display: block;
  }
  .avatar-upload-icon{
    font-size: 28px;
    color: #8c939d;
    width: 133px;
    height: 133px;
    line-height: 133px;
    text-align: center;
  }
}
</style>

<style scoped>
.avatar-upload >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-upload >>> .el-upload:hover {
  border-color: #409eff;
}
</style>